.gallery-video__box {
  padding-left: 28px;
  padding-right: 28px;
  text-align: center;

  @media (min-width: @tablet-width) {
    padding-left: 162px;
    padding-right: 162px;
    padding-bottom: 64px;
  }
}

.gallery-video__caption {
  font-weight: bold;
  font-size: 18px;
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 0.87;
  }
}

.gallery-video__text {
  text-align: center;

  @media (min-width: @tablet-width) {
    font-size: 21px;
  }
}

.gallery-video__player {
  margin-left: 20px;
  margin-right: 20px;
  text-align: center;
}


.gallery-video__poster {
  position: relative;

  @media (min-width: @tablet-width) {
    width: 648px;
    height: 500px;
    object-fit: cover;
  }

  @media (min-width: @desktop-width) {
    width: 800px;
  }
}
