.post {
  padding-top: 24px;
  position: relative;

  &::after {
    content: "";
    display: block;
    background-color: @grey;
    height: 1px;
    width: 100%;
    padding: 0;
    margin: 0;

    @media (min-width: @tablet-width) {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: @form-grey;
    padding: 0;
  }
}

.post--popular {
  @media (min-width: @tablet-width) {
    grid-column: 1/-1;
    grid-row: 1/2;
  }
}

.post__image {
  display: block;
  border: none;
  width: 100%;

  @media (min-width: @tablet-width) {
    grid-column: 1/-1;
  }

  @media (min-width: @desktop-width) {
    min-height: 260px;
    grid-row: 1/3;
  }
}

.post__text {
  @media (min-width: @tablet-width) {
    padding-left: 20px;
  }
}

.post__text-box {
  @media (min-width: @tablet-width) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 27px;
  }

  @media (min-width: @desktop-width) {
    color: @white;
    position: absolute;
    width: 100%;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.3);
    padding-top: 22px;
    padding-bottom: 15px;
    padding-right: 20px;
  }
}

.post__title {
  margin: 0;
  text-align: left;
  line-height: 1.5;
  margin-top: 15px;
}

.post__author {
  margin: 0;
  text-align: left;
  margin-bottom: 11px;
  text-transform: none;
}

.post__like {
  display: flex;
  margin-bottom: 25px;

  @media (min-width: @tablet-width) {
    margin: 0;
  }
}

.post__like-count {
  margin: 0;
  margin-left: 14px;
  font-weight: bold;
}

.post__like-link {
  .list-reset();

  @media (min-width: @desktop-width) {
    color: @white;
  }
}

.post__like-icon {
  opacity: 0.3;

  @media (min-width: @desktop-width) {
    fill: @white;
    opacity: 1;
  }
}

.post--popular .post__image {
  @media (min-width: @tablet-width) {
    min-height: 400px;
    width: 100%;
  }
}
